import * as THREE from 'three';
import { useRef } from 'react';
import { useFrame, useLoader } from 'react-three-fiber';

const handlePointerDown = (e)=>{
	//console.log(e.object);
	//  object  active   true  
	e.object.active = true;
	//      

	if (window.activeMesh) {
		scaleDown(window.activeMesh);
		window.activeMesh.active = false;
	}
	window.activeMesh = e.object;
}

const handlePointerEnter = (e)=>{
	e.object.scale.x = 1.5;
	e.object.scale.y = 1.5;
	e.object.scale.z = 1.5;
}

const handlePointerLeave = (e)=>{
	//    active  True  
	if (!e.object.active) {
		e.object.scale.x = 1;
		e.object.scale.y = 1;
		e.object.scale.z = 1;
	}
}

const scaleDown = (object)=>{
	object.scale.x = 1;
	object.scale.y = 1;
	object.scale.z = 1;
}

function Box(props) {
  const ref = useRef(null);
	const texture = useLoader(THREE.TextureLoader, `${process.env.PUBLIC_URL}/img/wood.jpg`);
	useFrame(()=>{
		ref.current.rotation.x += 0.01;
		ref.current.rotation.y += 0.01;
	})
	return (
		<mesh ref={ref} {...props}
			receiveShadow
			castShadow
			onPointerDown={handlePointerDown}
			onPointerEnter={handlePointerEnter}
			onPointerLeave={handlePointerLeave}
		>
			<boxGeometry />
			<meshPhysicalMaterial
				map={texture} 
				//   color='white' 
				//   roughness={0} clearcoat={1} transparent transmission={0.5} reflectivity={1} side={THREE.DoubleSide}
				//   metalness={1} roughness={0.5} clearcoat={0.5}
			/>
		</mesh>
	);
}

export default Box;